import React from "react";
import "./About.css";
import prashant from "..//..//img/prashant.png";
// import resume from "..//..//..//public/resume/resume"

const About = () => {

  const handleDownload = () => {
    // Replace 'your-resume-url.pdf' with the actual URL of your PDF file.
    const resumeUrl = "https://publuu.com/flip-book/266387/625799/resume.pdf";
    

    // Create an anchor element to trigger the download.
    const a = document.createElement('a');
    a.href = resumeUrl;
    a.download = 'resume.pdf';
    a.style.display = 'none';

    // Append the anchor element to the document and trigger the click event.
    document.body.appendChild(a);
    a.click();

    // Remove the anchor element after the download starts.
    document.body.removeChild(a);
  };
  return (
    <div id="About" className="about1">
      <div className="home-animation">
        <span>ABOUT</span>
      </div>
      <div className="about">
        <div className="left-1">
          <img src={prashant} alt="" />
        </div>
        <div className="right-about">
          <h1 style={{ fontWeight: "bolder", fontSize: "30px" }}>
            Need a Creative Product?
          </h1>
          <h1 style={{ fontSize: "30px", color: "#00C1FE" }}>I Can Help You</h1>
          <p style={{ fontSize: "15px" }}>
            Lorem ipsum dolor sit amet consectetur adipisicing elit. At
            voluptates illo officia voluptatem, voluptate consectetur. Tenetur,
            alias dolores praesentium nostrum eligendi adipisci quos sunt
            laboriosam tempora! Totam voluptas cupiditate perferendis?
          </p>
          <button className="button n-button">HIRE ME</button>
          
          <button style={{ marginLeft: "30px" }} className="button n-button" onClick={handleDownload}>
            DOWNLOAD CV
          </button>
         
          
        </div>
      </div>
    </div>
  );
};

export default About;
