import React from "react";
import "./Contact1.css";
import { useRef } from "react";
import { useState } from "react";
import emailjs from "@emailjs/browser";
import {
  GitHub,
  LinkedIn,
  Twitter,
  WhatsApp,
  YouTube,
} from "@material-ui/icons";


const Contact = () => {
  const form = useRef();

  let [done, setDone] = useState(false);


  const [formData, setFormData] = useState({
    // Initialize your form fields with default values
    name: '',
    email: '',
    // Add more fields as needed
  });



  const handleChange = (e) => {
    // Update the form data as the user types
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };


  const sendEmail = (e) => {
    e.preventDefault();


    setFormData({
      user_name: '',
      email: '',
      phone:"",
      message:""
    });

    emailjs
      .sendForm(
        "service_64t7djt",
        "template_qdqwuv6",
        form.current,
        "oofzBo9CuWYAerQHo"
      )
      .then(
        (result) => {
          console.log(result.text);
          setDone(true);
        },
        (error) => {
          console.log(error.text);
        }
      );
  };
  return (
    <>
    <div className="contact-animation" id="Contact">
      <span style={{}}>CONTACT ME</span>
    </div>
      <div className="contact">
        <div className="left">
          <div>
            <h2>Contact Me</h2>
          </div>
          <div>
            <h1 style={{   color: "#4f4f6c",
    fontweight: "bolder" }}>Address</h1>
            <span>
              63/C Daffodil nest House no202 2nd floor 4th main road talaquery
              layout basavnagar 560037
            </span>
          </div>
          <div style={{marginTop:"30px"}}>
            <h1 style={{   color: "#4f4f6c",fontweight: "bolder" }}>Phone</h1>
            <span>(+91)</span> <span> 8050157096</span> <br />
            <span>(+91)</span> <span> 9480198203</span>
          </div>
          <div >
            <h1 style={{   color: "#4f4f6c",
    fontweight: "bolder" }}>Email</h1>
            <span style={{display:"block"}}>Prashant.malge96@gamil.com</span>
            <span style={{display:"block",flexDirection:"block"}}>Prashantmalge787@gmail.com</span>
            <span>prashantmalge19378@gail.com</span>
          </div>
          <div className="socialicon" style={{marginTop:"20px"}}>
            {/* <SocialIcon color="55ACEE"> */}
            <a href="https://twitter.com/PrashantMa22817">
            <Twitter style={{ cursor: "pointer", backgroundColor:"#EFEFEF", color:"#1DA1F2",width:"25px", height:"25px",borderRadius:"50%", padding:"6px"  }}/>
            </a>
            {/* </SocialIcon> 
            {/* <SocialIcon color="3B5999"> */}
            <a href="https://www.linkedin.com/in/prashant-malge-761455166">
            <LinkedIn style={{ cursor: "pointer", backgroundColor:"#EFEFEF", color:"#4467AD",width:"25px", height:"25px",borderRadius:"50%", padding:"6px"  }}/>
            </a>
            {/* </SocialIcon> */}
            {/* <SocialIcon color="4078c0"> */}
            <a href="https://github.com/dashboard">
            <GitHub style={{ cursor: "pointer", backgroundColor:"#EFEFEF", color:"black",width:"25px", height:"25px",borderRadius:"50%", padding:"6px"  }}/>
            </a>
            {/* </SocialIcon> */}
            {/* <SocialIcon color="ff0000"> */}
            <a href="https://www.youtube.com/">
            <YouTube style={{ cursor: "pointer", backgroundColor:"#EFEFEF", color:"red",width:"25px", height:"25px",borderRadius:"50%", padding:"6px"  }}/>
            </a>
            {/* </SocialIcon> */}
            {/* <SocialIcon color="25D366"> */}
            <a href="https://wa.link/3au90s">
            <WhatsApp style={{ cursor: "pointer", backgroundColor:"#EFEFEF", color:"#00B63B",width:"25px", height:"25px",borderRadius:"50%", padding:"6px"  }} />
            </a>
            {/* </SocialIcon> */}
          </div>
        </div>
        <div className="right">
          <form ref={form} onSubmit={sendEmail}>
            {/* <label>Name</label> */}
            <input
              style={{ position: "relative" }}
              type="text"
              className="user"
              name="user_name"
              placeholder="Name"
              value={formData.user_name}
              onChange={handleChange}

            />

            {/* <label>Email</label> */}
            <>
              <input
                type="email"
                className="user"
                name="email"
                placeholder="email"
                value={formData.email}
              onChange={handleChange}
              />
            </>

            <input
              type="tel"
              className="user"
              name="phone"
              placeholder="Phone Number"
              value={formData.phone}
              onChange={handleChange}
            />
            {/* <label>Message</label> */}
            <textarea
              name="message"
              className="user"
              placeholder="Write Message"
              value={formData.message}
              onChange={handleChange}
            />
            <button className="button" type="submit">Submit</button>
            <span>{done && "thanks for contacting me"}</span>
            <div
              className="blur c-blur1"
              style={{ background: "var(--purple)" }}
            ></div>
          </form>
        </div>
      </div>
    </>
  );
};

export default Contact;
