import React from "react";
import "./Component1.css"
import aws from "..//..//img-1/aws.webp";
import gitHub from "..//..//img-1/github.png";
import linux from "..//..//img-1/linux.png";
import html from "..//..//img-1/html.webp";
import css from "..//..//img-1/css.png";
import js from "..//..//img-1/js.png";
import react from "..//..//img-1/react.svg";
import bootstrap from "..//..//img-1/bootstrap.png";
import devops from "..//..//img-1/devops.png";


const Component1 = () => {
    
  return (
    <div >
      <div className="right-skills" >
        <container className="right-skills-1">
          <div>
            <img
              src={html}
              alt=""
              style={{
                width: "120px",
                height: "120px",
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                outlineColor: "none",
              }}
            />
            <img
              src={react}
              alt=""
              width={150}
              height={120}
              style={{
                width: "120px",
                height: "120px",
                border: " white",
                backgroundColor: "red",
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                outlineColor: "none",
              }}
            />
            <img
              src={linux}
              alt=""
              width={150}
              height={100}
              style={{
                width: "120px",
                height: "120px",
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                outlineColor: "none",
              }}
            />
          </div>
          <div>
            <img
              src={css}
              alt=""
              width={120}
              height={120}
              style={{
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                outlineColor: "none",
              }}
            />
            <img
              src={bootstrap}
              alt=""
              style={{
                width: "120px",
                height: "120px",
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                backgroundColor: "red",
              }}
            />
            <img
              src={devops}
              alt=""
              width={150}
              height={100}
              style={{
                width: "120px",
                height: "120px",
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                outlineColor: "none",
              }}
            />
          </div>
          <div style={{ marginTop: "20px" }}>
            <img
              src={js}
              alt=""
              width={100}
              height={100}
              style={{
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                display: "flex",
                outlineColor: "none",
                background: "red",
                transforms: "scale(0.5)",
              }}
            />
            <img
              src={aws}
              alt=""
              width={100}
              height={100}
              style={{
                width: "120px",
                height: "120px",
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
              }}
            />
            <img
              src={gitHub}
              alt=""
              width={120}
              height={120}
              style={{
                width: "120px",
                height: "120px",
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                outlineColor: "none",
              }}
            />
          </div>
        </container>
      </div>
    </div>
  );
};

export default Component1;
