import React from "react";
import { Link } from "react-scroll";
import "./Navbar.css";
import Toggle from "../Toggle/Toggle";
import styled from "styled-components";
import {mobile} from "../../component/Responsive.js"
import { useState } from "react";

const Navbar = () => {
    const [filter, setFilter] = useState({})

  const handleFilter = (e) =>{
    const value = e.target.value
    setFilter({
      ...filter, 
      [e.target.name]: value,
    })
  }




  // const [colorChange, setColorchange] = useState(false);
	// const changeNavbarColor = () => {
	// 	if (window.scrollY >= 80) {
	// 		setColorchange(true);
	// 	}
	// 	else {
	// 		setColorchange(false);
	// 	}
	// };
	// window.addEventListener('scroll', changeNavbarColor);








    const Select = styled.select`
padding: 1px;
margin-left: 10px;
${mobile({margin: "10px 0px"})}
border-radius: 5px;
`;

const Option = styled.option``;


  return (
    <>
    <div /* className={colorChange ? 'navbar colorChange' : 'navbar'} style={{  position: "sticky",
   top:"0", zindex: "*10",backgroundcolor: "#000"}} */>
    <div className="n-wrapper">
        <div className="n-left">
        <div className="n-name" style={{fontSize:"16px"}}>PRASHANT</div>
          <Toggle />
        </div>
        <div className="n-right">
          <div className="n-list">
          <ul style={{ listStyletType: "none" }}>
          <Link
          spy={true}
          to="Navbar"
          smooth={true}
                activeclass="activeClass"
                >
                <li style={{ fontWeight: "bold",fontSize:"16px"}}>Home</li>
              </Link>
              <Link spy={true} to="Experience" smooth={true}>
                <li style={{ fontWeight: "bold",fontSize:"16px" }}>Experience</li>
              </Link>

              <Link spy={true} to="About" smooth={true}>
                <li style={{ fontWeight: "bold",fontSize:"16px" }}>About</li>
                </Link>
                <Link spy={true} to="Skills" smooth={true}>
                <li style={{ fontWeight: "bold",fontSize:"16px" }}>Skills</li>
                </Link>
                <Link spy={true} to="Contact" smooth={true}>
                <li style={{ fontWeight: "bold",fontSize:"16px" }}>Contact</li>
                </Link>
            </ul>
          </div>
          <Select name="Lan" onChange={handleFilter} style={{fontSize: "15px"}}>
          <Option show>Lan</Option>
          <Option>Eng</Option>
          <Option>Kan</Option>
          </Select>
          </div>
          </div>
          </div>
          </>
          );
};

export default Navbar;