import React from "react";
import prashant from "../../img/prashant.png"
import "./Introduction.css";
import { TypeAnimation } from 'react-type-animation';

const Intro = () => {
  return (
      <>
    <div className="intro">
      <div className="left">
      <TypeAnimation
      sequence={[
        ' Hy! I Am',
        1000, 
        'Prashant Malge',
        1000
      ]}
      wrapper="span"
      speed={300}
      style={{ fontSize: '2em', display: 'inline-block', color: "#00C1FE", fontWeight:"bolder"}}
      repeat={Infinity}
    />
        <h1 style={{color:"gray", opacity:"0.7", marginBottom:"20px", fontSize:"18px"}}>Frontend Developer with having good Experience and development,  producing
         the quality work</h1>
         <button  className='button n-button'>GET IN TOUCH</button>
      </div>
      <div className="right">
        <img src={prashant} alt={prashant} />
      </div>
    </div>




  </>
  );
};
export default Intro;
