import React from 'react'
// import aws from "..//..//img-1/aws.webp";
import gitHub from "..//..//img-1/github.png";
// import linux from "..//..//img-1/linux.png";
import insomnia from "..//..//img-1/insomnia.jpeg";
import vscode from "..//..//img-1/vscode.png";
import postman from "..//..//img-1/postman.png";
// import react from "..//..//img-1/react.svg";
import jira from "..//..//img-1/jira.jpg";
// import devops from "..//..//img-1/devops.png";

const Component2 = () => {
  return (
    <div>
            <div className="right-skills" >
        <container className="right-skills-1">
          <div>
            <img
              src={gitHub}
              alt=""
              style={{
                width: "120px",
                height: "120px",
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                outlineColor: "none",
              }}
            />
            <img
              src={insomnia}
              alt=""
              width={150}
              height={120}
              style={{
                width: "120px",
                height: "120px",
                border: " white",
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                outlineColor: "none",
              }}
            />
          </div>
          <div>
            <img
              src={vscode}
              alt=""
              width={120}
              height={120}
              style={{
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                outlineColor: "none",
              }}
            />
            <img
              src={jira}
              alt=""
              style={{
                width: "120px",
                height: "120px",
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
              }}
            />
          
          </div>
          <div style={{ marginTop: "20px" }}>
            <img
              src={postman}
              alt=""
              width={100}
              height={100}
              style={{
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                display: "flex",
                outlineColor: "none",
                transforms: "scale(0.5)",
              }}
            />
            <img
              src={gitHub}
              alt=""
              width={120}
              height={120}
              style={{
                width: "120px",
                height: "120px",
                borderRadius: "200px",
                objectFit: "cover",
                objectPosition: "center",
                outlineColor: "none",
              }}
            />
          </div>
        </container>
      </div>
    </div>
  )
}

export default Component2