import React from "react";
import { useState } from "react";
import "./Skill.css";

// import node from "..//..//img-1/node.png"
import Component1 from "./Component1";
import Component2 from "./Component2";

const Skill = () => {
  const [showComponent1, setShowComponent1] = useState(false);
  const [showComponent2, setShowComponent2] = useState(false);

  const toggleComponent1 = () => {
    setShowComponent1(true);
    setShowComponent2(false);
  };

  const toggleComponent2 = () => {
    setShowComponent1(false);
    setShowComponent2(true);
  };

  return (
    <div id="Skills">
      <div className="skill-animation">
        <span>SKILLS</span>
      </div>
      <div className="skill">
        <div className="left-skills">
          <div>
            <h1
              style={{
                fontSize: "35px",
                fontFamily: "monospace",
                color: "#00C1FE",
              }}
            >
              What My Programming
            </h1>
            <h1>Skills Included?</h1>
          </div>
          <div>
            <p>
              I develop Simple. intutive and responsive user interface that
              helps users get things done with less effort and time those
              technologies.
            </p>
            <h2 style={{color:"#dc8103", fontWeight:"bolder", fontSize:"15px"}}>Click Below to Know More About My Skills</h2>
          </div>
          <div class="scrolldown" data-gw-scrollto="#media" data-gw-scrollto-offset="-60">
	<div class="scrolldown__arrows">
		<div class="scrolldown__arrow scrolldown__arrow_1" style={{fontSize:"6px"}}></div>
		<div class="scrolldown__arrow scrolldown__arrow_2"></div>
		<div class="scrolldown__arrow scrolldown__arrow_3"></div>
	</div>
  &nbsp;
</div>
          <div className="toggle-skill">
            <button onClick={toggleComponent1} className="button">
              Skills
            </button>
            <button onClick={toggleComponent2} className="button">
              Tools
            </button>
          </div>
        </div>
        {showComponent1 && <Component1 />}
        {showComponent2 && <Component2 />}
      </div>
    </div>
  );
};

export default Skill;
